html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-family: "Amatic SC", cursive;
  font-size: 5.5rem;
  text-align: center;
  padding: 0;
  margin: 0 0 4rem 0;
}
@media (min-width: 768px) {
  h2 {
    font-size: 9rem;
  }
}

h3 {
  font-size: 3.2rem;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  color: #000;
  text-decoration: none;
}

img {
  display: block;
}

ul {
  padding: 0;
  margin: 0 0 0 3rem;
}

.header {
  padding: 1.3rem 0;
  background-color: #212529;
  transition: height 1s ease;
  position: sticky;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
}
@media (min-width: 768px) {
  .header {
    height: auto !important;
  }
  .header__contenedor {
    display: flex;
    justify-content: space-between;
  }
}
.header__contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.header__img {
  height: 80px;
  width: 95px;
  border-radius: 10%;
}
@media (min-width: 768px) {
  .header__img {
    height: 60px;
    width: 60px;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  transition-property: opacity;
  transition-duration: 500ms;
  transition-timing-function: ease;
}
.nav--collapsed {
  position: unset;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 768px) {
  .nav {
    width: 80%;
    flex-direction: row;
    justify-content: space-between;
    position: unset;
    visibility: visible;
    opacity: 1;
  }
}
.nav__link {
  display: block;
  color: #FFFFFF;
  font-family: "Amatic SC", cursive;
  font-size: 2.5rem;
  padding: 1rem;
  width: 100%;
  text-align: center;
  transition-property: background-color, color, opacity, border-radius;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}
.nav__link:first-of-type {
  margin-top: 1rem;
}
.nav__link:last-of-type {
  margin-top: 0;
}
.nav__link:hover {
  background-color: #FFFFFF;
  color: #000;
  opacity: 0.5;
  border-radius: 5px;
}
@media (min-width: 768px) {
  .nav__link {
    font-size: 3rem;
    width: auto;
    padding: 0.5rem 1.5rem;
    border-radius: 5px;
  }
  .nav__link:first-of-type {
    margin-top: 0;
  }
}
@media (min-width: 992px) {
  .nav__link {
    padding: 0.5rem 2rem;
    font-size: 4rem;
  }
}

.hamburguer {
  width: 80px;
  height: 80px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 300ms ease-in-out;
}
@media (min-width: 768px) {
  .hamburguer {
    display: none;
  }
}
.hamburguer__btn {
  width: 50px;
  height: 4px;
  background-color: #FFFFFF;
  border-radius: 10px;
  transition: all 300ms ease-in-out;
}
.hamburguer__btn::before, .hamburguer__btn::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 4px;
  background-color: #FFFFFF;
  border-radius: 10px;
  transition: all 300ms ease-in-out;
}
.hamburguer__btn::before {
  transform: translateY(-16px);
}
.hamburguer__btn::after {
  transform: translateY(16px);
}
.hamburguer__btn--collapsed {
  transform: translateX(-50px);
  background-color: transparent;
}
.hamburguer__btn--collapsed::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.hamburguer__btn--collapsed::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.carousel__slider {
  width: 100%;
  position: relative;
  height: 88vh;
  overflow: hidden;
}
.carousel__slide {
  width: 100%;
  height: 100%;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.carousel__slide:not(.carousel__slide:first-of-type) {
  transform: translateX(100%);
}
.carousel__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.nosotros {
  padding: 10rem 0 2rem 0;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.nosotros__header {
  font-size: 5.5rem;
  text-align: center;
  padding: 0;
  margin: 0 0 4rem 0;
}
@media (min-width: 768px) {
  .nosotros__header {
    font-size: 9rem;
  }
}
@media (min-width: 992px) {
  .nosotros__BH {
    display: flex;
    flex-direction: row-reverse;
    gap: 2rem;
  }
}
.nosotros__titulo {
  font-size: 2rem;
  margin: 0 0 1rem 0;
  text-align: center;
  font-family: "Lato", sans-serif;
}
.nosotros__titulo span {
  display: block;
}
@media (min-width: 768px) {
  .nosotros__titulo {
    font-size: 3rem;
  }
}
.nosotros__texto {
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
}
.nosotros__texto span {
  text-align: left;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .nosotros__texto span {
    text-align: center;
  }
}
@media (min-width: 1100px) {
  .nosotros__texto span {
    text-align: right;
  }
}
.nosotros__collage img {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: 50rem;
}
@media (min-width: 992px) {
  .nosotros__collage {
    width: 150%;
  }
}
.nosotros__ivonne {
  margin-top: 5rem;
}
@media (min-width: 1100px) {
  .nosotros__ivonne {
    display: grid;
    grid-template-columns: 2fr 1.3fr;
    gap: 1rem;
  }
}
.nosotros__imagen {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nosotros__imagen img {
  border-radius: 50%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 100%;
  max-width: 50rem;
  max-height: 50rem;
}

.servicios {
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 8rem 0 10rem 0;
}
.servicios__aval {
  background-color: #212529;
  padding: 1.5rem 0;
}
.servicios__aval p {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
}
.servicios__aval p span {
  display: block;
}
.servicios__header {
  margin: 4rem 0;
  line-height: 1.2;
}
.servicios .faciales__titulo, .servicios .corporales__titulo, .servicios .quirurgicos__titulo {
  font-size: 3rem;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 1rem;
}
.servicios__contactanos {
  margin: 5rem 0;
  display: flex;
  justify-content: center;
}
.servicios__contactanos:last-of-type {
  margin-bottom: 0;
}
.servicios__boton {
  border: 2px solid #9b84ab;
  padding: 20px 80px;
  color: #FFFFFF;
  text-decoration: none;
  background: #9b84ab;
  transition: color 0.5s, background 0.5s;
  border-radius: 30px;
  font-size: 30px;
  font-weight: bold;
  min-width: 140px;
  text-align: center;
}
.servicios__boton:hover {
  color: #800080;
  background-color: #FFFFFF;
}

.cardCarousel {
  position: relative;
}
.cardCarousel__container {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  scrollbar-width: none;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}
.cardCarousel__container::-webkit-scrollbar {
  display: none;
}
.cardCarousel__boton {
  height: 38px;
  width: 38px;
  color: white;
  background-color: rgba(0, 0, 0, 0.537254902);
  border-radius: 50%;
  border: 0px solid;
  margin: auto;
  position: absolute;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.cardCarousel__boton img {
  width: 10px;
}
.cardCarousel__boton--prev {
  top: 45%;
}
.cardCarousel__boton--next {
  top: 45%;
  right: 0;
}

.card {
  background-color: white;
  flex: 0 0 auto;
  margin-left: 15px;
  max-width: 100%;
  padding: 2rem;
  height: 57rem;
  margin-bottom: 1rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card:last-child {
  margin-right: 15px;
}
@media (min-width: 768px) {
  .card {
    max-width: 49%;
  }
}
@media (min-width: 992px) {
  .card {
    max-width: 42rem;
  }
}
.card__header {
  height: 50%;
}
.card__img {
  max-width: 300px;
  margin: 0 auto;
  border-radius: 50%;
}
.card__titulo {
  margin: 2rem 0;
  text-align: center;
  font-size: 18px;
}
.card__texto {
  margin: 0;
  line-height: 1.5;
}
.card__texto--credenciales {
  display: flex;
  flex-direction: column;
}
.card__texto--credenciales span {
  font-size: 14.5px;
}
.card--integrales {
  height: 85rem;
  margin-left: 0;
  max-width: 100%;
}
.card--integrales:last-of-type {
  margin-right: 0;
}
.card--integrales__header {
  height: 45%;
  max-height: 320px;
}
.card--integrales__img {
  max-width: 320px;
}
.card--integrales p {
  margin: 0 0 1rem 0;
  font-size: 14.5px;
}
.card--integrales__titulo {
  text-align: left;
}

.integrales {
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 10rem 0 9.5rem 0;
}
.integrales__header {
  margin: 0 0 4rem 0;
  text-align: center;
}
.integrales__info {
  text-align: center;
  line-height: 1.5;
  margin: 0 0 3rem 0;
}
@media (min-width: 768px) {
  .integrales__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
  }
}
@media (min-width: 992px) {
  .integrales__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1rem;
  }
}

.contacto {
  background-color: #D3C4DD;
  padding: 10rem 0;
}
.contacto__header {
  margin: 0 0 3rem 0;
}
.contacto__subtitulo {
  text-align: center;
  margin: 0 0 2rem 0;
}
.contacto__about {
  margin: 0 0 5rem 0;
}
@media (min-width: 768px) {
  .contacto__grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.contacto__sociales {
  margin-top: 5rem;
}

.formulario {
  background-color: #FFFFFF;
  border-radius: 10%;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  padding: 3rem;
  position: relative;
  width: 100%;
  max-width: 650px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 auto;
}
.formulario__dato {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  border-radius: 2px;
}
.formulario__dato input, .formulario__dato textarea {
  padding: 1rem;
}
.formulario__dato input:focus, .formulario__dato input:active, .formulario__dato textarea:focus, .formulario__dato textarea:active {
  border: 0;
  box-shadow: 0 0 5px #800080;
  outline: 0 none;
}
.formulario__dato label {
  display: block;
  margin-bottom: 0.5rem;
}
.formulario__dato span {
  display: block;
  margin: 0.3rem 0 0 1rem;
  color: #800080;
}
.formulario__submit {
  border: 2px solid #9b84ab;
  padding: 20px 80px;
  color: #FFFFFF;
  text-decoration: none;
  background: #9b84ab;
  transition: color 0.5s, background 0.5s;
  border-radius: 30px;
  font-size: 30px;
  font-weight: bold;
  min-width: 140px;
  text-align: center;
  padding: 0.5rem 1rem;
  font-weight: bold;
  font-size: 25px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
.formulario__submit:hover {
  color: #800080;
  background-color: #FFFFFF;
}